"use client";

import { useState } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { ChevronRight } from "lucide-react";
import { Progress } from "@/components/ui/progress"
import ShopDetailsDialog from "./shopDetailsDialog";
import { SearchBar } from "../toolbar/SearchBar";

export default function CustomerShopsTable({ shops }) {
    const [selectedShop, setSelectedShop] = useState(null);

    console.log(shops)

    if (!shops || shops.length === 0) {
        return (
            <Card className="rounded-2xl">
                <CardContent className="p-6 text-center text-sm text-muted-foreground">
                    No shop found.
                </CardContent>
            </Card>
        );
    }

    return (
        <>
        <SearchBar/>

            {/* Shop List */}
            <div className="space-y-3">
                {shops.map((item) => {
                    const percentage = Math.round(
                        (item.stampCount / item.shop.targetStamps) * 100
                    );

                    return (
                        <Card
                            key={item.id}
                            onClick={() => setSelectedShop(item)}
                            className="cursor-pointer active:scale-[0.98] transition-all duration-150 rounded-2xl"
                        >
                            <CardContent className="p-4">
                                <div className="flex justify-between items-center">
                                    <div>
                                        <h2 className="font-semibold">
                                            {item.shop.shopName}
                                        </h2>
                                        <p className="text-sm text-muted-foreground mt-1">
                                            {item.stampCount} / {item.shop.targetStamps} stamps
                                        </p>
                                    </div>

                                    <div className="flex items-center gap-2">
                                        <span className="text-sm font-medium">
                                            {percentage}%
                                        </span>
                                        <ChevronRight className="size-4 text-muted-foreground" />
                                    </div>
                                </div>

                                {/* Progress Bar */}
                                <div className="mt-3 h-2 bg-muted rounded-full overflow-hidden">
                                    <Progress value={percentage} />
                                </div>
                            </CardContent>
                        </Card>
                    );
                })}

                <Card
                    onClick={() => setSelectedShop(item)}
                    className="cursor-pointer active:scale-[0.98] transition-all duration-150 rounded-2xl"
                >
                    <CardContent className="p-4">
                        <div className="flex justify-between items-center">
                            <div>
                                <h2 className="font-semibold">
                                    {'Name'}
                                </h2>
                                <p className="text-sm text-muted-foreground mt-1">
                                    {5} / {10} stamps
                                </p>
                            </div>

                            <div className="flex items-center gap-2">
                                <span className="text-sm font-medium">
                                    {50}%
                                </span>
                                <ChevronRight className="size-4 text-muted-foreground" />
                            </div>
                        </div>

                        {/* Progress Bar */}
                        <div className="mt-3 h-2 bg-muted rounded-full overflow-hidden">
                            <Progress value={50} />
                        </div>
                    </CardContent>
                </Card>

                <Card
                    onClick={() => setSelectedShop(item)}
                    className="cursor-pointer active:scale-[0.98] transition-all duration-150 rounded-2xl"
                >
                    <CardContent className="p-4">
                        <div className="flex justify-between items-center">
                            <div>
                                <h2 className="font-semibold">
                                    {'Name'}
                                </h2>
                                <p className="text-sm text-muted-foreground mt-1">
                                    {5} / {10} stamps
                                </p>
                            </div>

                            <div className="flex items-center gap-2">
                                <span className="text-sm font-medium">
                                    {50}%
                                </span>
                                <ChevronRight className="size-4 text-muted-foreground" />
                            </div>
                        </div>

                        {/* Progress Bar */}
                        <div className="mt-3 h-2 bg-muted rounded-full overflow-hidden">
                            <Progress value={50} />
                        </div>
                    </CardContent>
                </Card>

                <Card
                    onClick={() => setSelectedShop(item)}
                    className="cursor-pointer active:scale-[0.98] transition-all duration-150 rounded-2xl"
                >
                    <CardContent className="p-4">
                        <div className="flex justify-between items-center">
                            <div>
                                <h2 className="font-semibold">
                                    {'Name'}
                                </h2>
                                <p className="text-sm text-muted-foreground mt-1">
                                    {5} / {10} stamps
                                </p>
                            </div>

                            <div className="flex items-center gap-2">
                                <span className="text-sm font-medium">
                                    {50}%
                                </span>
                                <ChevronRight className="size-4 text-muted-foreground" />
                            </div>
                        </div>

                        {/* Progress Bar */}
                        <div className="mt-3 h-2 bg-muted rounded-full overflow-hidden">
                            <Progress value={50} />
                        </div>
                    </CardContent>
                </Card>

                <Card
                    onClick={() => setSelectedShop(item)}
                    className="cursor-pointer active:scale-[0.98] transition-all duration-150 rounded-2xl"
                >
                    <CardContent className="p-4">
                        <div className="flex justify-between items-center">
                            <div>
                                <h2 className="font-semibold">
                                    {'Name'}
                                </h2>
                                <p className="text-sm text-muted-foreground mt-1">
                                    {5} / {10} stamps
                                </p>
                            </div>

                            <div className="flex items-center gap-2">
                                <span className="text-sm font-medium">
                                    {50}%
                                </span>
                                <ChevronRight className="size-4 text-muted-foreground" />
                            </div>
                        </div>

                        {/* Progress Bar */}
                        <div className="mt-3 h-2 bg-muted rounded-full overflow-hidden">
                            <Progress value={50} />
                        </div>
                    </CardContent>
                </Card>

                <Card
                    onClick={() => setSelectedShop(item)}
                    className="cursor-pointer active:scale-[0.98] transition-all duration-150 rounded-2xl"
                >
                    <CardContent className="p-4">
                        <div className="flex justify-between items-center">
                            <div>
                                <h2 className="font-semibold">
                                    {'Name'}
                                </h2>
                                <p className="text-sm text-muted-foreground mt-1">
                                    {5} / {10} stamps
                                </p>
                            </div>

                            <div className="flex items-center gap-2">
                                <span className="text-sm font-medium">
                                    {50}%
                                </span>
                                <ChevronRight className="size-4 text-muted-foreground" />
                            </div>
                        </div>

                        {/* Progress Bar */}
                        <div className="mt-3 h-2 bg-muted rounded-full overflow-hidden">
                            <Progress value={50} />
                        </div>
                    </CardContent>
                </Card>
            </div>

            {/* Dialog with Detailed Card */}
            <ShopDetailsDialog selectedShop={selectedShop} setSelectedShop={setSelectedShop} />
        </>
    );
}
